import React, { useEffect, useState } from "react";
import { StyleSheet, css } from "aphrodite/no-important";

const styles = StyleSheet.create({
	TimerContent: {
		backgroundColor: "rgba(255,255,255, 0.1)",
		padding: '1px 8px',
		marginRight: '1px',
		color: 'orange',
		borderRadius: '0.25rem'
	},
	colorOrange: {
		color: 'orange',
	},
	TimerSpan: {
		fontSize: '0.8rem',
		opacity: '0.6'
	}
})

const Timer = () => {
	const currentTime = new Date().getTime();
	const [distance, setDistance] = useState(new Date("Jan 1, 2022 22:55:00").getTime() - currentTime);
	const [day, setDay] = useState(Math.floor(distance / (1000 * 60 * 60 * 24)));
	const [hour, setHour] = useState(Math.floor(distance % (1000 * 60 * 60 * 24) / (1000 * 60 * 60)));
	const [min, setMin] = useState(Math.floor(distance % (1000 * 60 * 60) / (1000 * 60)));
	const [sec, setSec] = useState(Math.floor(distance % (1000 * 60) / 1000));
	useEffect(() => {
		setTimeout(() => {
			setDistance(distance - 1000);
			setDay(Math.floor(distance / (1000 * 60 * 60 * 24)));
			setHour(Math.floor(distance % (1000 * 60 * 60 * 24) / (1000 * 60 * 60)));
			setMin(Math.floor(distance % (1000 * 60 * 60) / (1000 * 60)));
			setSec(Math.floor(distance % (1000 * 60) / 1000));
		}, 1000);
	}, [distance, setDistance]);

	return (
		<div>
			{day &&
				<div className="d-flex justify-content-center mb-2 font-semibold">
					<div className="mx-1">
						<div className="d-flex">
							<div className={`${css(styles.TimerContent)}`} >
								{Math.floor(day / 10)}
							</div>
							<div className={`${css(styles.TimerContent)}`} >
								{day % 10}
							</div>
						</div>
						<span className={css(styles.TimerSpan)}>
							DAY
						</span>
					</div>
					<div>
						<div className="d-flex">
							<div className={`${css(styles.TimerContent)}`} >
								{Math.floor(hour / 10)}
							</div>
							<div className={`${css(styles.TimerContent)}`} >
								{hour % 10}
							</div>
						</div>
						<span className={css(styles.TimerSpan)}>
							HR
						</span>
					</div>
					<div className={css(styles.colorOrange)}>:</div>
					<div>
						<div className="d-flex">
							<div className={`${css(styles.TimerContent)}`} >
								{Math.floor(min / 10)}
							</div>
							<div className={`${css(styles.TimerContent)}`} >
								{min % 10}
							</div>
						</div>
						<span className={css(styles.TimerSpan)}>
							MIN
						</span>
					</div>
					<div className={css(styles.colorOrange)}>:</div>
					<div >
						<div className="d-flex">
							<div className={`${css(styles.TimerContent)}`} >
								{Math.floor(sec / 10)}
							</div>
							<div className={`${css(styles.TimerContent)}`} >
								{sec % 10}
							</div>
						</div>
						<span className={css(styles.TimerSpan)}>
							SEC
						</span>
					</div>
				</div>}
		</div>

	)
}
export default Timer;