import React, { useState } from "react";
import { Dropdown, DropdownButton } from 'react-bootstrap';
import { CgArrowsH } from 'react-icons/cg';
import { BsArrowLeft, BsPlusCircle, BsQuestionCircle } from 'react-icons/bs';
import etherIcon from '../assets/image/ethereum.png';
import polygonIcon from '../assets/image/polygon.png';
import usdcIcon from '../assets/svg/usdc.svg';
import Timer from './Timer';
import Prize from "./PrizeModal";
import "./Deposit.scss";

const EtherSVG = ({ size }) =>
	<img src={etherIcon} width={size} height={size} alt="Ether"/>

const PolygonSVG = ({ size }) =>
	<img src={polygonIcon} width={size} height={size} alt="Polygon" />

const UsdcSVG = ({ size }) =>
	<img src={usdcIcon} width={size} height={size} alt="USDC"/>
const Deposit = () => {
	const [type, setType] = useState(0);
	// const [title, setTitle] = useState(0);
	const [open, setOpen] = useState(false);

	const handleOpen = () => {
		setOpen(true)
	}

	return (
		<div className="Deposit">
			<div>
				<div className="InnerCard">
					<div className="Balance">$123,424</div>
					<div className="WeeklyPrize">IN WEEKLY PRIZES</div>
					<div className="DrawClose">DRAW #54 CLOSING IN</div>
					<Timer />
					{/* <p className="DailyPrize">VIEW DAILY PRIZES</p> */}
					<button className="DailyPrize" onClick={handleOpen}>VIEW DAILY PRIZES</button>
					<Prize open={open} setOpen={setOpen} />
				</div>
			</div>
			<div className="Card mt-3">
				<div className="DepositUSDC d-flex align-items-center">
					Deposit&nbsp;<UsdcSVG size={25} />
					&nbsp;USDC on&nbsp;

					<DropdownButton
						variant="secondary"
						menuVariant="dark"
						className="dropBtnContainer mt-2 "
						title={
							parseInt(type) === 0 ?
								<div className="dropBtnIcon">
									<EtherSVG size={15} />
									Ethereum
								</div> :
								<div className="dropBtnIcon">
									<PolygonSVG size={15} />
									Polygon
								</div>
						}
						onSelect={e => setType(e)}
					>
						<Dropdown.Item eventKey={0}><EtherSVG size={15} /> Ethereum</Dropdown.Item>
						<Dropdown.Item eventKey={1}><PolygonSVG size={15} /> Polygon</Dropdown.Item>
					</DropdownButton>
				</div>
				<div className="Amount">AMOUNT</div>
				<div className="position-relative">
					<div className="USDCInput align-items-center d-flex position-absolute">
						<UsdcSVG size={30} />
						USDC
					</div>
					<input type="text" placeholder="0.0" className="InputValue" />
					<div className="Issue">
						<div>
							Issues:
						</div>
						<div className="text-danger">
							Deposit amount required
						</div>
					</div>
					{/* <div className="NetworkFee">
                        <div>
                            Estimated network fees:
                        </div>
                        <div className="text-white">
                            $10
                        </div>
                    </div> */}
				</div>
				<div className="ConnectButton">Connect Wallet</div>
			</div>
			<div className="Support mt-1 py-3">
				<div className="col-sm-4"><CgArrowsH size={14} /> Bridge tokens</div>
				<div className="col-sm-4"><BsQuestionCircle size={14} /> Help</div>
				<div className="col-sm-4"><BsPlusCircle size={14} className="mr-3" /> Get tokens</div>
			</div>
			<div className="mt-5 p-2">
				This is PoolTogether V4, Previous pools are on V3.
			</div>
			<button className="BackTo"><BsArrowLeft size={14} /> Back to V3</button>
		</div>
	);
};

export default Deposit;
